/* eslint-disable */
Drupal.behaviors.header = {
  attach(context) {
    const header = context.querySelector('.header');
    const breakpointLg = getComputedStyle(
      document.documentElement,
    ).getPropertyValue('--breakpoint-large');
    if (header) {
      const headerHeight = header.offsetHeight;
      const headerNavHeight = header.querySelector('.header__menu')
        ? header.querySelector('.header__menu')?.offsetHeight
        : null;
      const headerMobile = context.querySelector('.header-mobile');

      const validSearchInHeader = () => {
        const headerSearch = context.querySelector('.header__inner__search');

        if (!headerSearch) {
          return;
        }

        if (window.innerWidth > breakpointLg) {
          const headerInner = header.querySelector('.header__inner');
          headerInner?.append(headerSearch);
        } else {
          const divSearchMobile = headerMobile.querySelector(
            '.header-mobile__search',
          );
          divSearchMobile?.append(headerSearch);
        }
      };

      const createHeaderMobile = () => {
        const headerLanguage = header.querySelector(
          '.header__primary__container-menu .gtranslate_wrapper',
        );
        const headerUpdate = header.querySelector(
          '.header__primary__container-menu .actualizacion',
        );
        console.log({ headerUpdate });
        const headerMapaSite = header.querySelector(
          '.header__primary__container-menu .menu',
        );
        if (window.innerWidth < breakpointLg) {
          const navbarMobile = header.querySelector('#main-nav');
          headerLanguage?.parentNode?.removeChild(headerLanguage);
          navbarMobile?.insertBefore(headerLanguage, navbarMobile.firstChild);
          headerUpdate?.parentNode?.removeChild(headerUpdate);
          navbarMobile?.insertBefore(headerUpdate, navbarMobile.firstChild);
          headerMapaSite?.parentNode?.removeChild(headerMapaSite);
          navbarMobile?.appendChild(headerMapaSite);
        }
        const headerLogoMobile = header.querySelector('.header__menu-image');
        const headerLogos = header.querySelectorAll('.header__inner__logo');

        const divLogosMobile = headerMobile.querySelector(
          '.header-mobile__logos',
        );

        if (divLogosMobile && divLogosMobile && headerLogoMobile) {
          // valid if exist
          if (divLogosMobile.children.length > 0) {
            return;
          }

          const logoMobile = headerLogoMobile?.cloneNode(true);
          divLogosMobile.appendChild(logoMobile);

          if (headerLogos.length > 1) {
            if (headerLogos.length === 2) {
              divLogosMobile.classList.add('header-mobile__logos--two');
            }

            if (headerLogos.length === 3) {
              divLogosMobile.classList.add('header-mobile__logos--three');
            }

            for (let i = 0; i < headerLogos.length; i++) {
              const logo = headerLogos[i];
              const logoMobile = logo.cloneNode(true);
              divLogosMobile.appendChild(logoMobile);
            }
          }
        }
      };

      if (headerMobile) {
        window.addEventListener('resize', () => {
          validSearchInHeader();
        });
        validSearchInHeader();
        createHeaderMobile();
      }

      document.documentElement.style.setProperty(
        '--size-menu',
        `${headerNavHeight ? headerNavHeight : '56'}px`,
      );

      window.onscroll = function () {
        if (
          window.scrollY >= headerHeight - headerNavHeight &&
          window.innerWidth > breakpointLg
        ) {
          header.classList.add('header--sticky');
        } else {
          header.classList.remove('header--sticky');
        }

        if (window.innerWidth < breakpointLg) {
          if (header.classList.contains('header--sticky')) {
            header.classList.remove('header--sticky');
          }
        }
      };
    }
  },
};
